import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  IconButton,
  Grid,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';
import { Appliance } from '../types/Appliance';

interface ConsumptionCalculatorProps {
  appliances: Appliance[];
  onApplianceRemove: (index: number) => void;
  onApplianceUpdate: (appliances: Appliance[]) => void;
  electricityRate: number;
  onRateChange: (rate: number) => void;
  isMobile: boolean;
  showResults: boolean;
  onShowResults?: () => void;
}

const ConsumptionCalculator: React.FC<ConsumptionCalculatorProps> = ({
  appliances,
  onApplianceRemove,
  onApplianceUpdate,
  electricityRate,
  onRateChange,
  isMobile,
  showResults,
  onShowResults,
}) => {
  const calculateDailyConsumption = (power: number, hours: number) => {
    return (power * hours) / 1000;
  };

  const calculateMonthlyCost = (dailyConsumption: number) => {
    return dailyConsumption * 30 * electricityRate;
  };

  const calculateHourlyConsumption = (power: number) => {
    return power / 1000; // Convert to kWh
  };

  const totalDailyConsumption = appliances.reduce((total, appliance) => {
    return total + calculateDailyConsumption(appliance.power, appliance.hours);
  }, 0);

  const totalHourlyConsumption = appliances.reduce((total, appliance) => {
    return total + calculateHourlyConsumption(appliance.power);
  }, 0);

  const totalMonthlyCost = calculateMonthlyCost(totalDailyConsumption);

  if (isMobile && showResults) {
    return (
      <Box className="mobile-page">
        <Box className="mobile-page-content">
          <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" gutterBottom align="center">
              סיכום צריכת חשמל
            </Typography>

            <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
              <TextField
                type="number"
                label="תעריף לקילוואט שעה (₪)"
                value={electricityRate}
                onChange={(e) => onRateChange(parseFloat(e.target.value) || 0)}
                fullWidth
                InputProps={{
                  startAdornment: <CurrencyExchangeIcon sx={{ mr: 1, color: 'text.secondary' }} />,
                }}
              />
              
              <TextField
                type="number"
                label="עדכן שעות לכל המכשירים"
                onChange={(e) => {
                  const hours = parseFloat(e.target.value) || 0;
                  const newAppliances = appliances.map(appliance => ({
                    ...appliance,
                    hours: hours
                  }));
                  onApplianceUpdate(newAppliances);
                }}
                sx={{ minWidth: '200px' }}
                inputProps={{ min: 0, max: 24, step: 0.5 }}
              />
            </Box>
            
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Paper sx={{ p: 2, bgcolor: 'primary.light', color: 'white' }}>
                  <Typography variant="subtitle1" gutterBottom align="center">
                    צריכה לשעה
                  </Typography>
                  <Typography variant="h4" align="center">
                    {totalHourlyConsumption.toFixed(2)} קוט"ש
                  </Typography>
                </Paper>
              </Grid>
              
              <Grid item xs={12}>
                <Paper sx={{ p: 2, bgcolor: 'primary.light', color: 'white' }}>
                  <Typography variant="subtitle1" gutterBottom align="center">
                    צריכה יומית
                  </Typography>
                  <Typography variant="h4" align="center">
                    {totalDailyConsumption.toFixed(2)} קוט"ש
                  </Typography>
                </Paper>
              </Grid>
              
              <Grid item xs={12}>
                <Paper sx={{ p: 2, bgcolor: 'success.light', color: 'white' }}>
                  <Typography variant="subtitle1" gutterBottom align="center">
                    עלות חודשית
                  </Typography>
                  <Typography variant="h4" align="center">
                    ₪{totalMonthlyCost.toFixed(2)}
                  </Typography>
                </Paper>
              </Grid>
            </Grid>
          </Paper>

          <TableContainer component={Paper}>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>מכשיר</TableCell>
                  <TableCell align="right">הספק</TableCell>
                  <TableCell align="right">שעות</TableCell>
                  <TableCell align="right">צריכה יומית</TableCell>
                  <TableCell align="right">עלות חודשית</TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {appliances.map((appliance, index) => {
                  const dailyConsumption = calculateDailyConsumption(appliance.power, appliance.hours);
                  const monthlyCost = calculateMonthlyCost(dailyConsumption);
                  return (
                    <TableRow key={index}>
                      <TableCell>{appliance.name}</TableCell>
                      <TableCell align="right">{appliance.power}W</TableCell>
                      <TableCell align="right">
                        <TextField
                          type="number"
                          value={appliance.hours}
                          onChange={(e) => {
                            const newAppliances = [...appliances];
                            newAppliances[index] = {
                              ...appliance,
                              hours: parseFloat(e.target.value) || 0
                            };
                            onApplianceUpdate(newAppliances);
                          }}
                          size="small"
                          sx={{ width: '70px' }}
                          inputProps={{ min: 0, max: 24, step: 0.5 }}
                        />
                      </TableCell>
                      <TableCell align="right">{dailyConsumption.toFixed(2)}</TableCell>
                      <TableCell align="right">₪{monthlyCost.toFixed(2)}</TableCell>
                      <TableCell>
                        <IconButton 
                          size="small" 
                          onClick={() => onApplianceRemove(index)}
                          sx={{ color: 'error.main' }}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          
          <Box sx={{ mt: 4, textAlign: 'center', color: 'text.secondary' }}>
            <Typography variant="body2">
              &copy; {new Date().getFullYear()} מתן כלף | כל הזכויות שמורות
            </Typography>
          </Box>
        </Box>
      </Box>
    );
  }

  return (
    <Box>
      <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
        <Typography variant="h6" gutterBottom align="center">
          סיכום צריכת חשמל
        </Typography>

        <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
          <TextField
            type="number"
            label="תעריף לקילוואט שעה (₪)"
            value={electricityRate}
            onChange={(e) => onRateChange(parseFloat(e.target.value) || 0)}
            fullWidth
            InputProps={{
              startAdornment: <CurrencyExchangeIcon sx={{ mr: 1, color: 'text.secondary' }} />,
            }}
          />
          
          <TextField
            type="number"
            label="עדכן שעות לכל המכשירים"
            onChange={(e) => {
              const hours = parseFloat(e.target.value) || 0;
              const newAppliances = appliances.map(appliance => ({
                ...appliance,
                hours: hours
              }));
              onApplianceUpdate(newAppliances);
            }}
            sx={{ minWidth: '200px' }}
            inputProps={{ min: 0, max: 24, step: 0.5 }}
          />
        </Box>
        
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <Paper sx={{ p: 2, bgcolor: 'primary.light', color: 'white' }}>
              <Typography variant="subtitle1" gutterBottom align="center">
                צריכה לשעה
              </Typography>
              <Typography variant="h4" align="center">
                {totalHourlyConsumption.toFixed(2)} קוט"ש
              </Typography>
            </Paper>
          </Grid>

          <Grid item xs={12} md={4}>
            <Paper sx={{ p: 2, bgcolor: 'primary.light', color: 'white' }}>
              <Typography variant="subtitle1" gutterBottom align="center">
                צריכה יומית
              </Typography>
              <Typography variant="h4" align="center">
                {totalDailyConsumption.toFixed(2)} קוט"ש
              </Typography>
            </Paper>
          </Grid>
          
          <Grid item xs={12} md={4}>
            <Paper sx={{ p: 2, bgcolor: 'success.light', color: 'white' }}>
              <Typography variant="subtitle1" gutterBottom align="center">
                עלות חודשית
              </Typography>
              <Typography variant="h4" align="center">
                ₪{totalMonthlyCost.toFixed(2)}
              </Typography>
            </Paper>
          </Grid>
        </Grid>

        <TableContainer component={Paper} sx={{ mt: 3 }}>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell>מכשיר</TableCell>
                <TableCell align="right">הספק</TableCell>
                <TableCell align="right">שעות</TableCell>
                <TableCell align="right">צריכה יומית</TableCell>
                <TableCell align="right">עלות חודשית</TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {appliances.map((appliance, index) => {
                const dailyConsumption = calculateDailyConsumption(appliance.power, appliance.hours);
                const monthlyCost = calculateMonthlyCost(dailyConsumption);
                return (
                  <TableRow key={index}>
                    <TableCell>{appliance.name}</TableCell>
                    <TableCell align="right">{appliance.power}W</TableCell>
                    <TableCell align="right">
                      <TextField
                        type="number"
                        value={appliance.hours}
                        onChange={(e) => {
                          const newAppliances = [...appliances];
                          newAppliances[index] = {
                            ...appliance,
                            hours: parseFloat(e.target.value) || 0
                          };
                          onApplianceUpdate(newAppliances);
                        }}
                        size="small"
                        sx={{ width: '70px' }}
                        inputProps={{ min: 0, max: 24, step: 0.5 }}
                      />
                    </TableCell>
                    <TableCell align="right">{dailyConsumption.toFixed(2)}</TableCell>
                    <TableCell align="right">₪{monthlyCost.toFixed(2)}</TableCell>
                    <TableCell>
                      <IconButton 
                        size="small" 
                        onClick={() => onApplianceRemove(index)}
                        sx={{ color: 'error.main' }}
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        
        <Box sx={{ mt: 4, textAlign: 'center', color: 'text.secondary' }}>
          <Typography variant="body2">
            &copy; {new Date().getFullYear()} מתן כלף | כל הזכויות שמורות
          </Typography>
        </Box>
      </Paper>
    </Box>
  );
};

export default ConsumptionCalculator;
